<template>
	<div id="eorder-refund">
		<public-header title="退款进度"></public-header>
		<div class="content">
			<div class="refund-list font-14" v-if="order.refund_status > 0">
				<div class="refund-item margin-10-b">
					<div class="padding-15 bg-danger">
						<div class="font-20 c-white">退款成功</div>
					</div>
					<van-cell-group>
						<van-cell title="退款金额">
							<div class="c-danger" slot="right-icon">{{Lang.dollarSign}}{{order.final_fee}}</div>
						</van-cell>
						<van-cell class="no-fix" title="退款账户" value="预存金余额" />
					</van-cell-group>
					<div class="margin-10-t bg-default" v-if="refunds.length > 0">
						<div class="padding-15-lr padding-15-t padding-5-b">退款流程</div>
						<van-steps direction="vertical" :active="refunds.length-1" active-color="#ff2d4b">
							<van-step v-for="logItem in refunds" :key="logItem.id">
								<div>{{logItem.title}}</div>
								<p class="margin-5-t font-12 c-gray">{{logItem.note}}</p>
								<div class="margin-5-t c-gray font-12">{{logItem.addtime_cn}}</div>
							</van-step>
						</van-steps>
					</div>
					<div class="margin-10-t bg-default padding-10-b">
						<div class="padding-15-lr padding-15">退款信息</div>
						<van-row class="padding-15-lr margin-10-t">
							<van-col span="5">订单号码</van-col>
							<van-col span="19">{{order.order_sn}}</van-col>
						</van-row>
					</div>
				</div>
			</div>
			<div class="loaded" v-else>
				<div class="loaded-tips">暂无退款记录</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData} from '@/controller/funcCommon'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				id: 0,
				refunds: [],
				order: {
					final_fee: 0,
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'errander/storeOrder/refund',
					data: {
						id: that.id
					},
					success: (res) => {
						if(res.refunds && res.refunds.length > 0) {
							that.refunds = [...res.refunds];
						}
						this.order = this.util.extend(this.order, res.order);
					}
				})
			}
		},
		mounted() {
			let query = this.util.parseQuery(this.$route.query);
			this.id = query.id;
			this.onLoad();
		}
	}
</script>

<style>
	#eorder-refund .refund-item .van-cell.no-fix .van-cell__value{
		flex: none;
		max-width: 90%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#eorder-refund .refund-item  .van-icon.van-icon-checked{
		background-color: #ff2d4b;
		color: #fff!important;
		border-radius: 50%;
	}
	#eorder-refund .refund-item .more{
		display: inline-flex;
		padding: 3px 10px;
		border-radius: 3px;
		font-size: 12px;
		border: 1px solid #999;
		margin-bottom: 10px;
	}
	#eorder-refund .refund-item .goods-list .goods-item{
		background-color: #f5f5f5;
		padding: 10px;
		margin-bottom: 10px;
	}
	#eorder-refund .refund-item .goods-list .goods-item .img-wrap{
		width: 50px;
		height: 50px;
		font-size: 0px;
	}
	#eorder-refund .refund-item .goods-list .goods-item .goods-info{
		height: 50px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: center;
	}
</style>


